import {PackageModel} from '../../model/package.js'
const packageModel = new PackageModel()
export default {
    name: "car_cosmetology",
    data(){
        return{
            info:[]
        }
    },
    methods:{
        _getInfo(params)
        {
            packageModel.getPackageInfo(params).then((res)=>{
                this.info = res.data;
            })
        }
    },
    onLoad(params){
        this._getInfo(params)
    }
}
